import React from 'react'
import './style.scss'
import './Bootstrap.min.scss'
import { Link } from 'react-router-dom'
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket'
import { useStateValue } from './StateProvider'

function Header(props) {
	const [{ basket }] = useStateValue()
	return (
		<>
			{/* Topbar Start */}
			<div className="container-fluid px-0 d-none d-lg-block ">
				<div className="row gx-0">
					<div className="col-lg-4 text-center bg-secondary py-3">
						<div className="d-inline-flex align-items-center justify-content-center">
							<i className="bi bi-envelope fs-1 text-primary me-3" />
							<div className="text-start">
								<h6 className="text-uppercase mb-1">
									Email Us
								</h6>
								<span>info@example.com</span>
							</div>
						</div>
					</div>
					<div className="col-lg-4 text-center bg-primary border-inner py-3">
						<div className="d-inline-flex align-items-center justify-content-center">
							<Link to="/" className="navbar-brand ">
								<h1 className="m-0 text-uppercase text-white">
									<i className="fa fa-birthday-cake fs-1 text-dark me-3" />
									{props.name}
								</h1>
							</Link>
						</div>
					</div>
					<div className="col-lg-4 text-center bg-secondary py-3">
						<div className="d-inline-flex align-items-center justify-content-center">
							<i className="bi bi-phone-vibrate fs-1 text-primary me-3" />
							<div className="text-start">
								<h6 className="text-uppercase mb-1">Call Us</h6>
								<span>+012 345 6789</span>
							</div>
						</div>
					</div>
				</div>
			</div>
			{/* Topbar End */}
			{/* Navbar Start */}
			<nav className="navbar navbar-expand-lg bg-dark navbar-dark shadow-sm py-3 py-lg-0 px-3 px-lg-0 sticky-sm-top">
				<a href="index.html" className="navbar-brand d-block d-lg-none">
					<h1 className="m-0 text-uppercase text-white">
						<i className="fa fa-birthday-cake fs-1 text-primary me-3" />
						{props.name}
					</h1>
				</a>
				<button
					className="navbar-toggler"
					type="button"
					data-bs-toggle="collapse"
					data-bs-target="#navbarCollapse"
				>
					<span className="navbar-toggler-icon" />
				</button>
				<div className="collapse navbar-collapse" id="navbarCollapse">
					<div className="navbar-nav ms-auto mx-lg-auto py-0">
						<Link to="/home" className="nav-item nav-link  active">
							Home
						</Link>
						<Link to="/about" className="nav-item nav-link">
							About Us
						</Link>
						<Link to="/menu1" className="nav-item nav-link">
							Menu &amp; Pricing
						</Link>
						<Link to="/chef" className="nav-item nav-link">
							Master Chefs
						</Link>
						<div className="nav-item dropdown">
							<Link
								to="/"
								className="nav-link dropdown-toggle"
								data-bs-toggle="dropdown"
							>
								Pages
							</Link>
							<div className="dropdown-menu m-0">
								<Link to="/services" className="dropdown-item">
									Our Service
								</Link>
								<Link
									to="/testimonial"
									className="dropdown-item"
								>
									Testimonial
								</Link>
							</div>
						</div>
						<Link to="/contact" className="nav-item nav-link">
							Contact Us
						</Link>
						<Link to="/Checkout" className="nav-item nav-link position-absolute top-0 end-0 ">
							<ShoppingBasketIcon />
							<span className="option-two header-optionCount">
								{basket?.length}
							</span>
						</Link>
					</div>
				</div>
			</nav>
			{/* Navbar End */}
		</>
	)
}

export default Header
